<template>
  <div id="app" :class="{'say':'no'}" v-on:click="Arrangement()">
    <HelloWorld/>
    <Contents v-bind:contents="contents"></Contents>
    <Say v-bind:next="next" @find="find"></Say>
  </div>
</template>

<script>
  import HelloWorld from './components/HelloWorld.vue'
  import Say from "./components/Say.vue";
  import Contents from "./components/Contents.vue";

  export default {
    name: 'app',
    components: {
      HelloWorld,
      Contents,
      Say,
    },
    data(){
      return{
        message:null,
        contents:null,
        next:null
      }
    }
    ,
    mounted: function() {
      {
        this.$http({
          url:'dialog.json',
          method:'GET',
        }).then(
                function (res) {
                this.message=res.body;
                this.Start('0');
                }
        )
      }
    },
    methods:{
      Start:function (n) {
        let k=this.message.liujundong[n];
        this.contents=k.details[0];
        this.next=k.responses;
        window.console.log(this.contents);
      },
      find:function (data,res) {
        let n=data.toString();
        let k=this.message.liujundong[n];
        let a=k.details[0].toString();
        res='1'+res;
        this.contents.push(res);
        this.contents.push(a);
        this.next=this.message.liujundong[n].responses;
        window.console.log(res);
      },
      apush:function (n) {
        this.contents.push(n);
      }


    }
  }
</script>

<style scoped>
  #app {
    max-width:375px;
    width: 360px;
    left: 50%;
    margin-left:-180px;
    position: absolute;
    overflow: hidden;
  }
</style>
