<template>
       <div id="body">
              <transition-group name="list" tag="p">

              <div v-for="content in contents"  class="clear" :key="content">


                     <div v-if="content.charAt(0)==1" class="you">
                            {{content=content.substr(1)}}
                     </div>
                     <div v-else class="me">
                            {{content}}
                     </div>

              </div>

              </transition-group>
       </div>

</template>

<script>
    export default {
           name: "Contents",
           data(){
                  return{
                         messages:this.contents,
                  }
           },
           props: ['contents'],
           methods: {
                  test: function () {
                         window.console.log(this.messages);
                  }
           },
           updated: function(){
                  document.getElementById('body').scrollTop = document.getElementById('body').scrollHeight;
           }




    }
</script>

<style scoped>
       .list-enter-active, .list-leave-active {
              transition: all 1s;
       }
       .list-enter, .list-leave-to
              /* .list-leave-active for below version 2.1.8 */ {
              opacity: 0;
              transform: translateY(30px);
       }



  #body{
        background: #ededed;
        width: 100%;
        display: flex;
        flex-direction: column;
        height: 600px;
        overflow-y:scroll;
           padding-bottom: 20px;
    }
    /*整个滚动条样式*/
    #body::-webkit-scrollbar {
        width: 6px;
        height: 0px;
    }
    /*设置滚动条上的滚动滑块样式*/
    #body::-webkit-scrollbar-thumb {
        background: transparent;
    }
    .me{
        margin-top: 20px;
        background: #ffffff;
        padding: 5px;
        border-radius: 7px;
        margin-left: 15px;
           float: left;
           max-width: 200px;
    }
    .you {
        background: #9eea6a;
        padding: 5px;
        border-radius: 7px;
        margin-top: 20px;
           float: right;
        margin-right: 10px;
        position: relative;
    }
       .clear{
              clear:both;
       }

</style>