<template>
    <div id="say">
        <div class= "boxsay" :class="{'box':box}">

           <div v-for="u  in next" :key="u.id" v-on:click="say(u.nextliu,u.content)">
               {{u.content}}{{u.id}}
           </div>


            <div class="close" v-on:click="talk(true)">❎</div>
        </div>
        <div class="say" v-on:click="talk(false)">说点什么...</div>


    </div>
</template>

<script>
    export default {
        name: "Say",
        data(){
            return{
                box:1
            }
        },
        props: ['next']
        ,
        methods:{
           talk:function (n) {
               this.box = n;
           },
            say:function (n,k) {
                this.$emit('find',n,k)
                this.talk(true);
            }

        }
    }

</script>

<style scoped>
    #say{
        border-top:1px solid #f3f3f3;
        color: #bbb;
        border-bottom:1px solid #f3f3f3;
        background: #ffffff;
        width: 100%;
        height: 40px;
        line-height: 40px;
    }
    .say{
        margin-left: 20px;
    }
    .box{
        -webkit-transform:translateY(100%);
        -ms-transform:translateY(100%);
        transform:translateY(100%);
    }
    body .boxsay,.box{

        position:absolute;
        bottom:0;
        background: white;
        z-index:100;
        width:100%;
        -webkit-box-shadow:0 0 30px 0 rgba(0,0,0,0.2);
        box-shadow:0 0 30px 0 rgba(0,0,0,0.2);
        -webkit-transition-duration:.3s;
        transition-duration:.3s;
        -webkit-transition-property:-webkit-transform;
        transition-property:-webkit-transform;
        transition-property:transform;
        transition-property:transform,-webkit-transform;
        display: flex;
        flex-direction: column-reverse;
    }
    .close{
        text-align: right;
       padding-right: 20px;
    }
    .boxsay div{
        border-bottom: 1px solid #f3f3f3;
        padding: 5px;
    }
</style>